<template>
  <div>
    <top/>
    <!--<topMember v-on:member="getInfor"></topMember>-->
    <div class="box mar fffbg" style="min-height:500px;">
      <div class="mainH">
        <div class="mainHICO"></div>
        <span class="pull-left">关于我们</span>
      </div>
      <div style="padding:40px; font-size: 1.2rem">
        <p>　　石家庄彬尚有限公司是一家电商软件服务及商品贸易的科技公司，通过产品和服务，帮助全球电商商家更简单、更高效做生意。我们致力于成为电商领域里值得信任和依赖的合作伙伴，帮助商家获得更大的成功。</p>
        <br/>
        <p>公司地址：河北省石家庄藁城区梅花镇梅花村思贤街南行105米路南</p>
        <br/>
        <p>电话：{{number}}</p>
        <img style="width: 100%" src="../../static/business_license.png"/>
      </div>
    </div>
    <foot></foot>


  </div>
</template>

<script>
import foot from "./public/foot.vue";
import top from "./public/top.vue";
import topMember from "./public/topMember.vue";
export default {
  components: {
    foot,
    topMember,
    top
  },
  data() {
    return {
      number:'',
    };
  },
  mounted(){
    this.getNumber();
  },
  methods:{
    getNumber(){
      this.axios
        .get(
          // process.env.API_HOST+"user/me",
          "/giftmall/number",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao.tianyalei.com",
              domain: this.url,
            }
          }
        )
        .then(response=> {
          if (response.data.code == 200&&response.data.data) {
            this.number = response.data.data
          }
        });
    },
  }
};
</script>

<style scoped>


</style>




